@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/poppins/v20/pxiByp8kv8JHgFVrLEj6Z1xlFQ.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/poppins/v20/pxiEyp8kv8JHgFVrJJfecg.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-stretch: 100%;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/opensans/v29/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsjZ0B4gaVI.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  margin: 0;
  padding: 2.25rem;
  color: white;
}

footer .footer__attribution {
  font-size: 1rem;
  text-align: center;
}
footer .footer__attribution a {
  color: #ffee00;
}

h1,
h2,
h3 {
  font-family: "Poppins";
  font-weight: 600;
  margin-top: 0;
  line-height: 1.1;
}

.text__desc {
  font-family: "Open Sans";
  font-weight: 400;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper {
  display: flex;
  flex-flow: column nowrap;
  margin: 0 auto;
}
@media (max-width: 39.9375em) {
  .wrapper {
    background: #674baf url("/app/asset/images/bg-mobile.svg") left top/100% auto no-repeat;
  }
}
@media (min-width: 40em) {
  .wrapper {
    background: #674baf url("/app/asset/images/bg-desktop.svg") left top/auto 100% no-repeat;
    padding: 2.75rem 4rem;
  }
}

.content {
  display: flex;
  flex-flow: column nowrap;
  flex: 1 1 auto;
}
@media (min-width: 40em) {
  .content {
    flex-flow: row nowrap;
    justify-content: center;
  }
}

.footer {
  font-family: "Poppins";
  font-weight: 400;
  display: flex;
  flex-flow: row nowrap;
  margin-top: 3rem;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.content__container {
  margin-top: 3.5rem;
}
.content__container .container__hero {
  display: block;
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.content__text {
  margin-top: 4rem;
  text-align: center;
}
.content__text .text__heading {
  font-size: 1.5rem;
  line-height: 1.5;
}
.content__text .text__desc {
  font-family: "Open Sans";
  font-weight: 400;
  font-size: 1rem;
  margin: 1rem 0;
}
.content__text .text__cta {
  cursor: pointer;
  font-family: "Open Sans";
  font-weight: 400;
  font-size: 0.8125rem;
  color: #674baf;
  border: 0 solid white;
  border-radius: 50px;
  background-color: white;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.24);
  padding: 0.75rem 4.75rem;
  margin-top: 0.625rem;
  transition: all 0.35s ease;
}
.content__text .text__cta:hover {
  background-color: #e882e8;
  color: white;
}

.footer__icons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
i {
  cursor: pointer;
  display: inline-block;
  padding: 0.75rem;
  border-radius: 50%;
  border: 1px solid white;
  color: white;
  width: 40px;
  height: 40px;
  transition: all 0.35s ease;
}
i:not(:first-child) {
  margin-left: 0.75rem;
}
i:hover {
  border: 1px solid #e882e8;
  color: #e882e8;
}

.header__page-logo {
  width: 134px;
  margin-left: -0.3125rem;
}
@media (min-width: 40em) {
  .header__page-logo {
    width: 268px;
  }
}

@media (min-width: 40em) {
  .content {
    align-items: center;
    gap: 5rem;
  }
  .content .content__container {
    flex: 1 1 55%;
    align-self: stretch;
  }
  .content .content__text {
    flex: 1 1 45%;
    margin: 0 0 2rem 0;
    text-align: left;
  }
  .content .content__text .text__heading {
    font-size: min(max(1rem, 3.5vw), 2.5rem);
  }
  .content .content__text .text__desc {
    font-size: 1.15rem;
    line-height: 1.7;
  }
  .content .content__text .text__cta {
    font-size: 1rem;
    padding: 1rem 4rem;
  }
}
.footer {
  display: flex;
}
@media (max-width: 39.9375em) {
  .footer {
    flex-flow: column nowrap;
  }
  .footer .footer__content {
    display: none;
  }
}
@media (min-width: 40em) {
  .footer {
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    margin: 0;
  }
}
@media (min-width: 71.875em) {
  .footer .footer__content {
    display: contents;
  }
}